import React, { Component } from "react";

class Timer extends Component {
  render() {
    return (
      <div id="timer" className={this.props.hidden ? "null" : "hidden"}>
        <div style={{ color: this.props.alarmColor }}>
          <p id="timer-label">
            {this.props.status === "work" ? "Work Time" : "Time to Break"}
          </p>
          <p id="time-left">{this.props.clockify}</p>
        </div>
        <div id="timer-buttons">
          <button
            className="bttn-material-flat bttn-md bttn-no-outline"
            id="start_stop"
            onClick={this.props.timerControl}
            style={{ backgroundColor: this.props.alarmColor }}
          >
            <i
              className={
                this.props.timerState === "stopped"
                  ? "fa fa-play"
                  : "fa fa-pause"
              }
            />
          </button>
          <button
            className="bttn-material-flat bttn-md bttn-no-outline"
            onClick={this.props.select_session}
            style={{ backgroundColor: this.props.alarmColor }}
          >
            Work Time
          </button>
          <button
            className="bttn-material-flat bttn-md bttn-no-outline"
            onClick={this.props.select_break}
            style={{ backgroundColor: this.props.alarmColor }}
          >
            Break Time
          </button>
        </div>
      </div>
    );
  }
}

export default Timer;
