import React, { Component } from "react";
import "./App.css";
import accurateInterval from "./functions/accurate-interval.js";
import Config from "./components/Config.js";
import Timer from "./components/Timer.js";
import Footer from "./components/Footer.js";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hidden: true,
      status: "work",
      break_length: 5,
      session_length: 25,
      time_left: {
        min: 25,
        sec: 0
      },
      timer: 1500,
      timerState: "stopped",
      intervalID: "",
      alarmColor: "#005792"
    };
    this.change = this.change.bind(this);
    this.session_increment = this.session_increment.bind(this);
    this.session_decrement = this.session_decrement.bind(this);
    this.break_decrement = this.break_decrement.bind(this);
    this.break_increment = this.break_increment.bind(this);
    this.select_session = this.select_session.bind(this);
    this.select_break = this.select_break.bind(this);
    this.reset = this.reset.bind(this);
    this.timerControl = this.timerControl.bind(this);
    this.beginCountDown = this.beginCountDown.bind(this);
    this.decrementTimer = this.decrementTimer.bind(this);
    this.phaseControl = this.phaseControl.bind(this);
    this.warning = this.warning.bind(this);
    this.buzzer = this.buzzer.bind(this);
    this.switchTimer = this.switchTimer.bind(this);
    this.clockify = this.clockify.bind(this);
  }
  timerControl() {
    console.log("Here");
    let control =
      this.state.timerState === "stopped"
        ? (this.beginCountDown(), this.setState({ timerState: "running" }))
        : (this.setState({ timerState: "stopped" }),
          this.state.intervalID && this.state.intervalID.cancel());
  }
  beginCountDown() {
    console.log("Here now");
    this.setState({
      intervalID: accurateInterval(() => {
        this.decrementTimer();
        this.phaseControl();
      }, 1000)
    });
  }
  decrementTimer() {
    this.setState({ timer: this.state.timer - 1 });
  }
  phaseControl() {
    let timer = this.state.timer;
    this.warning(timer);
    this.buzzer(timer);
    if (timer < 0) {
      if (this.state.status === "work") {
        this.state.intervalID && this.state.intervalID.cancel();
        this.beginCountDown();
        this.switchTimer(this.state.session_length * 60, "work");
      } else {
        this.state.intervalID && this.state.intervalID.cancel();
        this.beginCountDown();
        this.switchTimer(this.state.session_length * 60, "work");
      }
    }
  }
  warning(_timer) {
    let warn =
      _timer < 61
        ? this.setState({ alarmColor: "#a50d0d" })
        : this.setState({ alarmColor: "#005792" });
  }
  buzzer(_timer) {
    if (_timer === 3) {
      this.audioBeep.play();
    }
  }
  switchTimer(num, str) {
    this.setState({
      timer: num,
      status: str,
      alarmColor: "#005792"
    });
  }
  clockify() {
    let minutes = Math.floor(this.state.timer / 60);
    let seconds = this.state.timer - minutes * 60;
    seconds = seconds < 10 ? "0" + seconds : seconds;
    minutes = minutes < 10 ? "0" + minutes : minutes;
    return minutes + ":" + seconds;
  }
  change() {
    this.state.hidden
      ? this.setState({ hidden: false })
      : this.setState({ hidden: true });
  }
  session_increment() {
    if (this.state.session_length < 60) {
      this.setState({
        session_length: this.state.session_length + 1
      });
      if (this.state.status === "work") {
        this.setState({
          timer: (1 + this.state.session_length) * 60,
          timerState: "stopped"
        });
        this.state.intervalID && this.state.intervalID.cancel();
      }
    }
  }
  session_decrement() {
    if (this.state.session_length > 1) {
      this.setState({
        session_length: this.state.session_length - 1
      });
      if (this.state.status === "work") {
        this.setState({
          timer: (this.state.session_length - 1) * 60,
          timerState: "stopped"
        });
        this.state.intervalID && this.state.intervalID.cancel();
      }
    }
  }
  break_increment() {
    if (this.state.break_length < 60) {
      this.setState({
        break_length: this.state.break_length + 1
      });
      if (this.state.status === "break") {
        this.setState({
          timer: (1 + this.state.break_length) * 60,
          timerState: "stopped"
        });
        this.state.intervalID && this.state.intervalID.cancel();
      }
    }
  }
  break_decrement() {
    if (this.state.break_length > 1) {
      this.setState({
        break_length: this.state.break_length - 1
      });
      if (this.state.status === "break") {
        this.setState({
          timer: (this.state.break_length - 1) * 60,
          timerState: "stopped"
        });
        this.state.intervalID && this.state.intervalID.cancel();
      }
    }
  }
  select_break() {
    this.setState({
      status: "break",
      timer: this.state.break_length * 60,
      timerState: "stopped"
    });
    this.state.intervalID && this.state.intervalID.cancel();
  }
  select_session() {
    this.setState({
      status: "work",
      timer: this.state.session_length * 60,
      timerState: "stopped"
    });
    this.state.intervalID && this.state.intervalID.cancel();
  }
  reset() {
    this.setState({
      status: "work",
      session_length: 25,
      break_length: 5,
      timer: 1500,
      timerState: "stopped",
      alarmColor: "#005792"
    });
    this.state.intervalID && this.state.intervalID.cancel();
  }
  render() {
    return (
      <div id="wrapper">
        <div id="pomodoro-box">
          <button
            className="bttn-material-flat bttn-md bttn-no-outline"
            id="cog-btn"
            onClick={this.change}
            style={{ backgroundColor: this.state.alarmColor }}
          >
            <i className="fa fa-cog" />
          </button>
          <Config
            hidden={this.state.hidden}
            alarmColor={this.state.alarmColor}
            session_increment={this.session_increment}
            session_decrement={this.session_decrement}
            session_length={this.state.session_length}
            break_length={this.state.break_length}
            break_increment={this.break_increment}
            break_decrement={this.break_decrement}
            reset={this.reset}
          />
          <Timer
            status={this.state.status}
            alarmColor={this.state.alarmColor}
            hidden={this.state.hidden}
            timerState={this.state.timerState}
            clockify={this.clockify()}
            timerControl={this.timerControl}
            select_break={this.select_break}
            select_session={this.select_session}
          />
        </div>
        <Footer />
        <audio
          id="beep"
          preload="auto"
          src="https://goo.gl/65cBl1"
          ref={audio => {
            this.audioBeep = audio;
          }}
        />
      </div>
    );
  }
}

export default App;
