import React, { Component } from "react";

class Config extends Component {
  render() {
    return (
      <div
        id="config"
        className={this.props.hidden ? "hidden" : null}
        style={{ color: this.props.alarmColor }}
      >
        <div id="session">
          <p id="session-label">Session Length</p>
          <button
            className="bttn-material-flat bttn-md bttn-no-outline"
            id="session-increment"
            onClick={this.props.session_increment}
            style={{ backgroundColor: this.props.alarmColor }}
          >
            <i class="fa fa-arrow-up" />
          </button>
          <p id="session-length">{this.props.session_length} </p>
          <button
            className="bttn-material-flat bttn-md bttn-no-outline"
            id="session-decrement"
            onClick={this.props.session_decrement}
            style={{ backgroundColor: this.props.alarmColor }}
          >
            <i class="fa fa-arrow-down" />
          </button>
        </div>
        <div id="break">
          <p id="break-label">Break Length</p>
          <button
            className="bttn-material-flat bttn-md bttn-no-outline"
            id="break-increment"
            onClick={this.props.break_increment}
            style={{ backgroundColor: this.props.alarmColor }}
          >
            <i class="fa fa-arrow-up" />
          </button>
          <p id="break-length">{this.props.break_length}</p>
          <button
            className="bttn-material-flat bttn-md bttn-no-outline"
            id="break-decrement"
            onClick={this.props.break_decrement}
            style={{ backgroundColor: this.props.alarmColor }}
          >
            <i class="fa fa-arrow-down" />
          </button>
        </div>
        <div id="reset-container">
          <button
            id="reset"
            onClick={this.props.reset}
            className="bttn-material-flat bttn-md bttn-no-outline"
            style={{ backgroundColor: this.props.alarmColor }}
          >
            <i className="fa fa-refresh" /> Reset
          </button>
        </div>
      </div>
    );
  }
}

export default Config;
